import React, { Component } from "react";
import {BrowserRouter as Router, Switch, Route} from "react-router-dom";
import 'bootstrap/dist/css/bootstrap.min.css';
import NavigationBar from "./components/commons/NavigationBar";
import Dashboard from "./components/Dashboard/Dashboard";
import Company from "./components/Company/Company";
import Schedule from "./components/Schedule/Schedule";
import '../node_modules/bootstrap/dist/css/bootstrap.css';
import '../node_modules/bootstrap/dist/js/bootstrap.js';
import Profile from "./components/Profile/Profile";
import Login from "./components/Login/LoginPage";

import { MuiPickersUtilsProvider } from '@material-ui/pickers';
import EmailSubmissionForm from "./components/EmailSubmission/EmailSubmissionForm";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      logoUrl : require('./img/logo.png'),
      loggedIn: false
    }
  }

  renderContent(){
    // if(this.state.loggedIn){
    //   return (
    //       <RouteComponent/>
    //   )
    // } else{
    //   return <Login/>
    // }
    return <RouteComponent/>
  }
  render(){
    return this.renderContent();
  }
}

function RouteComponent() {
  return (
      <Router>
        <div>
          <NavigationBar/>

          {/* A <Switch> looks through its children <Route>s and
            renders the first one that matches the current URL. */}
          <Switch>
            <Route path="/profile">
              <Profile/>
            </Route>
            <Route path="/schedule">
              <Schedule />
            </Route>
            <Route path="/company">
              <Company />
            </Route>
            <Route path="/email-submission">
              <EmailSubmissionForm />
            </Route>
            <Route path="/portal-login">
              <Login/>
            </Route>
            <Route path="/">
              <Dashboard />
            </Route>
          </Switch>
        </div>
      </Router>
  );
}

export default App;
