import React, {useState} from 'react';
import clsx from 'clsx';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelActions from '@material-ui/core/ExpansionPanelActions';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Chip from '@material-ui/core/Chip';
import Button from '@material-ui/core/Button';
import Divider from '@material-ui/core/Divider';
import Input from '@material-ui/core/Input';
import DateFnsUtils from '@date-io/date-fns'; // choose your lib

import {DatePicker, MuiPickersUtilsProvider} from '@material-ui/pickers';


const BasicInfo = ({classes}) => {

  const [editable, setEditable] = useState(false);
  const [selectedDate, handleDateChange] = useState(new Date());


  return (
    <ExpansionPanel DefaultExpanded>
      <ExpansionPanelSummary  expandIcon={<ExpandMoreIcon />} aria-controls="panel1c-content" id="panel1c-header">
        <div className={classes.column}>
          <Typography className={classes.heading}>Basic Information</Typography>
          <Divider/>
        </div>
      </ExpansionPanelSummary>
      <ExpansionPanelDetails className={classes.details}>
        <div className="container">
          <div className={classes.rowPadding}>
            <div className="row">
              <div className='col-md-6'>
                <Typography className={classes.labelPadding}>Full name: </Typography>

              </div>
              <div className='col-md-6'>
                <Input defaultValue="" inputProps={{ 'aria-label': 'description' }} />

              </div>
            </div>
          </div>
          <div className={classes.rowPadding}>
            <div className="row">
              <div className='col-md-6'>
                <Typography className={classes.labelPadding}>Preferred name: </Typography>

              </div>
              <div className='col-md-6'>
                <Input defaultValue="" inputProps={{ 'aria-label': 'description' }} />

              </div>
            </div>
          </div>
          <div className={classes.rowPadding}>
            <div className="row">
              <div className='col-md-6'>
                <Typography className={classes.labelPadding}>Date of Birth: </Typography>

              </div>
              <div className='col-md-6'>
                 <MuiPickersUtilsProvider utils={DateFnsUtils}>
                <DatePicker
                    disableFuture
                    openTo="year"
                    format="dd/MM/yyyy"
                    views={["year", "month", "date"]}
                    value={selectedDate}
                    onChange={handleDateChange}
                  />
              </MuiPickersUtilsProvider>
              </div>
            </div>
          </div>
          <div className={classes.rowPadding}>
            <div className="row">
              <div className='col-md-6'>
                <Typography className={classes.labelPadding}>Teams: </Typography>

              </div>
              <div className='col-md-6'>
                <div className={classes.labelPadding}>
                  <span className="badge badge-pill table-data-span">
                    HR App <span className="circle-style" style={{'backgroundColor': 'red'}}>
                  </span>
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </ExpansionPanelDetails>
    <Divider />
    <ExpansionPanelActions>
      {
        editable ?
        (
          <div>
            <Button size="small" onClick={() => setEditable(!editable)}>Cancel</Button>
            <Button size="small" color="primary">Save</Button>
          </div>

        ) : (
          <Button size="small" onClick={() => setEditable(!editable)}>Edit</Button>
        )
      }
    </ExpansionPanelActions>
    </ExpansionPanel>
  )
}

export default BasicInfo;
